.project-wrapper {
    position: relative;

    .portfolio-carousel-active {
        .single-project {
            overflow: hidden;

            .project-contents {
                background-color: #f7f7f7;
                border-radius: 10px;
                position: relative;
                margin-top: 60px;
            }

            .project-details-2 {
                padding: 50px 0px;

                @media (max-width: 991px) {
                    padding: 20px;
                }

                .project-meta {
                    .project-cat {
                        margin-right: 30px;
                    }

                    .client-info {
                        span {
                            color: #000;
                            font-weight: 600;
                            padding-right: 5px;
                        }
                        i {
                            color: #000;
                            font-weight: 500;
                        }
                    }
                }

                h2 {
                    font-size: 35px;
                    line-height: 50px;
                    margin-top: 5px;

                    @media (max-width: 1199px) {
                        font-size: 32px;
                        line-height: 130%;
                    }

                    @media (max-width: 767px) {
                        font-size: 30px;
                    }
                }

                p {
                    margin-top: 15px;
                }

                .read-btn {
                    margin-top: 35px;
                }
            }

            .project-details {
                padding: 100px 0px;

                @media (max-width: 991px) {
                    padding: 50px;
                }

                .project-meta {
                    .project-cat {
                        margin-right: 30px;
                    }

                    .client-info {
                        span {
                            color: #000;
                            font-weight: 600;
                            padding-right: 5px;
                        }
                        i {
                            color: #000;
                            font-weight: 500;
                        }
                    }
                }

                h2 {
                    font-size: 40px;
                    line-height: 50px;
                    margin-top: 5px;

                    @media (max-width: 1199px) {
                        font-size: 32px;
                        line-height: 130%;
                    }

                    @media (max-width: 767px) {
                        font-size: 30px;
                    }
                }

                p {
                    margin-top: 15px;
                }

                .read-btn {
                    margin-top: 35px;
                }
            }

            .project-thumbnail {
                position: relative;
                height: 512px;
                background-color: #dddddd;
                border-radius: 10px;
                margin-top: -60px;
                overflow: hidden;
                background-size: cover;

                @media (max-width: 991px) {
                    border-radius: 0px;
                    height: 300px;
                    background-position: center;
                }

                @media (max-width: 767px) {
                    height: 250px;
                }

                .popup-gallery {
                    display: block;
                    width: 100%;
                    height: 100%;

                    img {
                        max-width: 100%;
                        width: 100%;
                    }
                }
            }
        }
    }

    .project-carousel-nav {
        display: flex;
        position: absolute;
        right: 95px;
        bottom: 0;
        z-index: 9999;
        overflow: hidden;
        border-radius: 10px;
        transform: translateY(50%);

        @media (max-width: 991px) {
            right: 50px;
        }

        div {
            width: 80px;
            height: 80px;
            line-height: 80px;
            text-align: center;
            font-size: 24px;
            color: #fff;
            transition: all 0.4s ease-in-out;
            background: $theme-color;
            cursor: pointer;

            @media (max-width: 991px) {
                width: 60px;
                height: 60px;
                line-height: 60px;
                font-size: 20px;
            }

            &.owl-next {
                background: $theme-dark;
            }
        }
    }
}

