.portfolio-cat-filter {
    &.style-2 {
        button {
            background-color: #fff;
        }
    }

    button {
        border: none;
        background-color: #f6f6f6;
        display: inline-block;
        padding: 15px 35px;
        line-height: 1;
        text-transform: capitalize;
        transition: all 0.3s ease-in-out;
        border-radius: 30px;
        margin: 0px 3px;
        font-weight: 600;
        margin-top: 30px;

        @media (max-width: 767px) {
            padding: 15px 25px;
            font-size: 12px;
            margin-top: 10px;
        }

        &.btn-active,
        &:hover {
            color: #fff;
            background-color: $theme-color-2;
        }
    }
}
@media screen and (min-width:700px){
    .desktop-portfolio3{
        background-color: transparent;
        margin-top:-300px;
    }
}
.portfolio-2-img{
    height: 145px;
    width: 150px;
}
@media screen and (max-width:700px){
    .portfolio-2-img{
        height: 160px;
        width: 190px;
    }
}

.portfolio-carousel-wrapper {
    &.owl-carousel {
        z-index: 0;
    }

    .center {
        .single-project-item {
            .project-details {
                transform: translateY(0);
                opacity: 1;
                visibility: visible;
            }
            .project-details-2 {
                transform: translateY(0);
                opacity: 1;
                visibility: visible;
            }
        }
    }

    .single-project-item {
        padding-bottom: 30px;
        margin-top: 30px;

        &:hover {
            .project-details {
                transform: translateY(0);
                opacity: 1;
                visibility: visible;
            }
            .project-details-2 {
                transform: translateY(0);
                opacity: 1;
                visibility: visible;
            }
        }

        .project-thumb {
            height: 450px;
            background-color: #eee;
            position: relative;

            @media (max-width: 991px) {
                height: 400px;
            }
        }
        .project-thumb-2 {
            height: 400px;
            background-color: #eee;
            position: relative;
            padding: 0.5rem 0;

            @media (max-width: 991px) {
                height: 420px;
            }
        }

        .project-details {
            position: relative;
            background-color: #fff;
            width: 90%;
            margin: 0px auto;
            z-index: 3;
            box-shadow: $box-shadow;
            padding: 25px 35px;
            margin-top: -60px;
            z-index: 1;
            opacity: 0;
            visibility: hidden;
            transition: all 0.4s ease-in-out;
            transform: translateY(30px);

            &::before {
                position: absolute;
                width: 90%;
                left: 50%;
                bottom: -10px;
                transform: translateX(-50%);
                background-color: $theme-color-3;
                z-index: -1;
                content: "";
                height: 10px;
            }

            .icon {
                display: inline-block;
                position: absolute;
                right: 30px;
                top: 0;
                padding: 10px 30px;
                background-color: $theme-color-3;
                color: #001248;
                text-align: center;
                transform: translateY(-50%);
                -webkit-clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
                clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
            }

            .meta {
                span {
                    &:last-child {
                        color: #001248;
                        font-weight: 600;
                    }
                }
            }

            h3 {
                color: #001248;
                transition: all 0.3s ease-in-out;

                &:hover {
                    color: $theme-color-3;
                }
            }
        }

        .project-details-2 {
            position: relative;
            background-color: #fff;
            width: 90%;
            margin: 0px auto;
            z-index: 3;
            box-shadow: $box-shadow;
            padding: 10px 10px;
            margin-top: -50px;
            z-index: 1;
            opacity: 1;
            visibility: visible;
            transition: all 0.4s ease-in-out;
            transform: translateY(30px);

            &::before {
                position: absolute;
                width: 90%;
                left: 50%;
                bottom: -10px;
                transform: translateX(-50%);
                background-color: $theme-color-3;
                z-index: 1;
                content: "";
                height: 10px;
            }

            .icon {
                display: inline-block;
                position: absolute;
                right: 30px;
                top: 0;
                padding: 10px 30px;
                background-color: $theme-color-3;
                color: #001248;
                text-align: center;
                transform: translateY(-50%);
                -webkit-clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
                clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
            }

            .meta {
                span {
                    &:last-child {
                        color: #001248;
                        font-weight: 600;
                    }
                }
            }

            h3 {
                color: #001248;
                transition: all 0.3s ease-in-out;

                &:hover {
                    color: $theme-color-3;
                }
            }
            a{
                font-size: 20px;
            }
        }
    }
}


.portfolio-wrapper-section {
    position: relative;
    z-index: 1;
    overflow: hidden;

    &::before {
        position: absolute;
        width: 150%;
        height: 60%;
        left: -10px;
        bottom: -20%;
        content: "";
        background-color: $section-bg;
        transform: rotate(-11deg);
        z-index: -1;

        @media (max-width: 991px) {
            transform: rotate(0);
            bottom: 0;
            left: 0;
            height: 40%;
            width: 100%;
        }
    }
}

.single-cause-item {
    margin-top: 30px;
    transition: all 0.3s ease-in-out;

    &:hover {
        .cause-bg {
            &::before {
                background-color: $theme-color-2;
            }
            &::after {
                background-color: #e24700;
            }

            .icon {
                opacity: 1;
                visibility: visible;
                transform: translateY(0);
            }
        }

        .cause-content {
            background-color: $theme-color-2;

            .cause-meta {
                color: #fff;
            }
        }
    }

    .cause-bg {
        height: 250px;
        position: relative;
        overflow: hidden;
        width: 100%;

        &::before {
            position: absolute;
            width: 200%;
            height: 90px;
            left: -10px;
            bottom: -10px;
            content: "";
            background-color: $second-color;
            transform: rotate(-15deg);
            z-index: 1;
            transition: all 0.3s ease-in-out;

            @media (max-width: 991px) {
                transform: rotate(-8deg);
            }

            @media (max-width: 767px) {
                display: none;
            }
        }

        &::after {
            position: absolute;
            width: 200%;
            height: 107px;
            left: -22px;
            bottom: -10px;
            content: "";
            background-color: $theme-color-2;
            transform: rotate(-19deg);
            transition: all 0.3s ease-in-out;

            @media (max-width: 991px) {
                transform: rotate(-11deg);
            }

            @media (max-width: 767px) {
                display: none;
            }
        }

        .icon {
            position: absolute;
            border-radius: 50%;
            background-color: #fff;
            width: 60px;
            height: 60px;
            line-height: 60px;
            text-align: center;
            color: $second-color;
            z-index: 3;
            right: 30px;
            bottom: 0;
            content: "";
            font-size: 18px;
            opacity: 0;
            visibility: hidden;
            transform: translateY(115%);
            transition: all 0.4s ease-in-out;
        }
    }

    .cause-content {
        padding: 20px 35px 30px;
        background-color: $second-color;
        transition: all 0.3s ease-in-out;

        .cause-meta {
            color: #a5adc6;
            transition: all 0.3s ease-in-out;
        }

        h4 {
            color: #fff;
        }
    }
}

.single-approch-card,
.single-service-card {
    padding: 15px;
    background-color: #fff;
    position: relative;
    box-shadow: 0px 30px 60px 0px rgba(52, 52, 52, 0.04);
    margin-top: 30px;

    .card-thumb {
        height: 200px;
        position: relative;
        transition: all 0.3s ease-in-out;
    }

    .content {
        padding: 30px 35px;
        background-color: #f7f7f7;
        position: relative;

        @media (max-width: 600px) {
            padding: 30px;
        }

        .case-cat {
            position: absolute;
            right: 0;
            bottom: 100%;

            a {
                display: inline-block;
                padding: 20px;
                line-height: 1;
                font-size: 24px;
                background-color: $theme-color;
                position: relative;

                &:hover {
                    color: #fff;
                    background: $theme-color-2;
                }
            }
        }

        h3 {
            font-size: 22px;
            margin-bottom: 5px;
            text-transform: capitalize;

            &:hover {
                a {
                    color: $theme-color;
                }
            }
        }

        .read-btn {
            font-size: 16px;
            line-height: 1;
            text-transform: capitalize;
            margin-top: 20px;
            display: block;

            &:hover {
                color: $theme-color-3;
                padding-left: 5px;
            }

            i {
                margin-left: 5px;
            }
        }
    }
}
