.career-cards-wrapper {
    display: flex;
    flex-direction: column;
    grid-gap: 20px;
    gap: 20px;
    padding: 20px 0;
}



.career-card {
    display: flex;
    align-items: flex-start;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.1);
    padding: 20px;
    width: 100%;
    overflow: hidden;
    transition: transform 0.3s ease;
}

.career-card:hover {
    transform: scale(1.02);
}

.career-card-image {
    width: 150px;
    height: 150px;
    object-fit: cover;
    border-radius: 10px;
    margin-right: 20px;
}

.career-card-content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    flex-grow: 1;
}

.career-card-title {
    font-size: 1.5rem;
    font-weight: bold;
    color: #333;
    margin: 0;
}

.career-card-abbreviation {
    font-size: 1.2rem;
    font-weight: 500;
    color: #555;
    margin: 5px 0 15px 0;
}

.career-card-description {
    font-size: 1rem;
    color: #666;
    line-height: 1.5;
    overflow: hidden;
    text-overflow: ellipsis;
}

.read-more {
    color: #007bff;
    cursor: pointer;
    font-weight: bold;
}

.auto-marquee-section {
    padding: 40px 0;
    background-color: #f8f8f8; /* Add background or use your desired styling */
}

.auto-marquee-container {
    overflow-x: hidden;
    white-space: nowrap;
    width: 100%;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
}

.auto-marquee-content {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    min-width: -webkit-max-content;
    min-width: max-content; /* Ensures the container is wide enough for scrolling */
}

/* Ensure ServicesOneCard fits inside the marquee smoothly */
.auto-marquee-container .single-service-card {
    flex: 0 0 auto; /* Prevent shrinking or growing */
    min-width: 300px; /* Adjust card size */
    max-width: 300px;
}

.auto-marquee-container img {
    width: 100%;
    height: auto;
    object-fit: cover;
}

/* Adjust the scrolling speed */
.auto-marquee-container {
    scroll-behavior: smooth;
}

/* Responsive design adjustments */
@media (max-width: 768px) {
    .auto-marquee-container {
        /* Adjust the speed or size of cards on smaller screens */
    }

    .auto-marquee-container .single-service-card {
        min-width: 250px; /* Smaller card size on mobile */
    }
}

/* Container styles */
.product-slider-container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 20px;
    background-color: #f8f9fa;
    overflow: hidden;
    position: relative;
}

/* Marquee container for infinite scrolling */
.marquee-container {
    display: flex;
    grid-gap: 20px;
    gap: 20px;
    animation: scroll 50s linear infinite;
    white-space: nowrap;
}

.marquee-pause {
    animation-play-state: paused;
}

/* Keyframe for infinite scrolling */
@keyframes scroll {
    0% {
        transform: translateX(0);
    }
    100% {
        transform: translateX(-100%);
    }
}

/* Card Styles */
.product-card {
    position: relative;
    width: 200px;
    height: 320px;
    border-radius: 12px;
    overflow: hidden;
    background: #ffffff;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    cursor: pointer;
    flex-shrink: 0;
}

.product-card:hover {
    transform: translateY(-5px);
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.2);
}

/* Image container */
.card-thumb {
    height: 60%;
    overflow: hidden;
    border-radius: 12px 12px 0 0;
}

.card-thumb img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: transform 0.3s ease;
}

.product-card:hover .card-thumb img {
    transform: scale(1.1);
}

/* Content */
.content {
    padding: 15px;
    text-align: center;
}

.content h3 {
    font-size: 16px;
    font-weight: 600;
    margin-bottom: 10px;
    color: #333333;
}

.content .read-btn {
    font-size: 14px;
    color: #007bff;
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 5px;
}

.content .read-btn svg {
    margin-left: 5px;
    transition: transform 0.2s ease;
}

.content .read-btn:hover svg {
    transform: translateX(5px);
}

/* src/FacebookFeed.css */

.facebook-feed-container {
  width: 100%;
  margin: 20px auto;
  padding: 30px;
  /* background: linear-gradient(145deg, #e0e7ff, #ffffff); */
  border-radius: 20px;
  box-shadow: 0 15px 30px rgba(0, 0, 0, 0.15);
  position: relative;
  overflow: hidden;
  text-align: center;
  font-family: Arial, sans-serif;
  animation: fadeIn 1.5s ease;
}

@keyframes fadeIn {
  0% { opacity: 0; transform: scale(0.9); }
  100% { opacity: 1; transform: scale(1); }
}

.feed-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 25px;
  border-bottom: 1px solid #dfe3e6;
}

.page-profile-img {
  width: 60px;
  height: 60px;
  border-radius: 50%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s;
}

.page-profile-img:hover {
  transform: scale(1.15);
}

.page-name {
  font-size: 24px;
  font-weight: bold;
  color: #334e68;
  flex-grow: 1;
  text-align: left;
  margin-left: 20px;
}

.follow-button {
  background: linear-gradient(145deg, #4267B2, #3b5998);
  color: #ffffff;
  border: none;
  border-radius: 50px;
  padding: 10px 20px;
  cursor: pointer;
  font-size: 16px;
  font-weight: 500;
  box-shadow: 0 4px 12px rgba(66, 103, 178, 0.3);
  transition: all 0.3s ease;
}

.follow-button:hover {
  background: linear-gradient(145deg, #365899, #2d4373);
  transform: translateY(-3px);
  box-shadow: 0 6px 15px rgba(66, 103, 178, 0.5);
}

.posts-marquee {
  overflow: hidden;
  margin-top: 25px;
  padding: 20px 0;
  border-radius: 10px;
  background: #f0f4f8;
  box-shadow: inset 0 5px 10px rgba(0, 0, 0, 0.1);
  position: relative;
}

.posts-row {
  display: flex;
  grid-gap: 15px;
  gap: 15px;
  animation: smoothScroll 45s linear infinite;
}

@keyframes smoothScroll {
  0% { transform: translateX(0); }
  100% { transform: translateX(-100%); }
}

.post-card {
  background: linear-gradient(135deg, #ffffff, #f7f9fc);
  border-radius: 15px;
  width: 300px;
  min-width: 280px;
  height: 350px;
  margin: 0 15px;
  padding: 20px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
  align-items: center;
  position: relative;
  transition: transform 0.4s ease, box-shadow 0.4s ease;
}

.post-card:hover {
  transform: translateY(-8px) scale(1.05);
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.25);
}

.post-image {
  width: 100%;
  height: 60%;
  border-radius: 10px;
  object-fit: cover;
  box-shadow: 0 6px 15px rgba(0, 0, 0, 0.2);
  transition: transform 0.3s ease;
}

.post-image:hover {
  transform: scale(1.1);
}

.post-message {
  font-size: 16px;
  color: #555;
  margin-top: 15px;
  text-align: center;
  line-height: 1.5;
  overflow: hidden;
  text-overflow: ellipsis;
  transition: color 0.3s;
}

.post-card:hover .post-message {
  color: #334e68;
}

.facebook-icon {
  color: #4267B2;
  font-size: 32px;
  margin-top: 20px;
  transition: transform 0.3s, color 0.3s;
}

.facebook-icon:hover {
  color: #2d4373;
  transform: scale(1.2);
}

