.search-dropdown{
  position: relative;
  background: #c7c7c7;
  border-radius: 5px 0px 0px 5px;
  color: rgb(77, 77, 77)
}

.search-dropdown-value{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 10px 10px;
  cursor: pointer;
  overflow: hidden;
  white-space: nowrap;
  width: 60px;
}

.search-dropdown-item-container{
  position: absolute;
  z-index: 99999;
  background-color: #f7f7f7;
  border-radius: 5px;
  white-space-collapse: nowrap;
  white-space: nowrap;
  margin-top: 10px;
  top: 100%;
  left: 0;
  opacity: 0;
  box-shadow: 0px 0px 5px 0px rgba(0, 0, 0, 0.582);
  animation: fade-in 0.3s ease-in 0.1s forwards;
}

@keyframes fade-in {
  from {
    transform: translateY(-10px);
    opacity: 0;
  }
  to {
    transform: translateY(0px);
    opacity: 1;
  }
  
}

.search-dropdown-item-container ul{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  padding: 0;
  margin: 0;
}

.search-dropdown-item{
  padding: 5px 20px;
  cursor: pointer;
  transition: all 0.5s ease;
  display: flex;
  width: 100%;
  margin:0;
  border-bottom: 1px solid rgb(215, 215, 215);

}
.search-dropdown-item:first-child{
  /* border-bottom: none; */
  border-radius: 5px 5px 0px 0px;
} 
.search-dropdown-item:last-child{
  border-bottom: none;
  border-radius: 0px 0px 5px 5px;
}



.search-dropdown-item:hover{
  background-color: #3C62A9;
  color: rgb(248, 248, 248);

}


